
"use client";

import Button from "@/components/componentTag/button";
import React, { useState } from "react";
import { useRouter} from "next/router";

export default function CardPage() {

    const router = useRouter();

    const [showAddModal, setShowAddModal] = useState(false)
    const [newTitle, setNewTitle] = useState(false);
    const [cards, setCards] = useState([
        { id: 1, nameCard: "Card Demo 1", numberOfTasks: 2 },
        { id: 2, nameCard: "Card Demo 2", numberOfTasks: 6 },
        { id: 3, nameCard: "Card Demo 2", numberOfTasks: 5 },
        { id: 4, nameCard: "Card Demo 2", numberOfTasks: 6 },
        { id: 5, nameCard: "Card Demo 2", numberOfTasks: 8 },
    ])

    const handleCreateCard = () => {
        setShowAddModal(true);
    }

    const handleAddCard = () => {
    if (newTitle.trim() === "") return;
    const newCard = {
      id: cards.length + 1,
      title: newTitle
    };
    setCards([...cards, newCard]);
    setNewTitle("");
    setShowAddModal(false);
  };


  return (
    <div className="p-4 h-full flex flex-col">
        <div className="flex justify-end mb-2">
            <Button 
                label={"+ Card"}
                onClick={handleCreateCard}
                className={"w-[81px] h-[30px] flex justify-center items-center"}
            />
        </div>
        <div className="grid lg:grid-cols-3 gap-2">
            {cards.map((card) => (
                <div 
                    key={card.id}
                    className="grid grid-cols-2"    
                >
                    <div className=" mr-2">
                        <div className="flex flex-rows">
                            <p>Card name:</p>
                            <h2>{card.nameCard}</h2>
                        </div>
                        <div className="flex flex-rows">
                            <p>Number of tasks:</p>
                            <p>{card.numberOfTasks}</p>
                        </div>
                    </div>
                    <Button 
                        label={"detail"}
                        onClick={() => router.push(`/dashboard/card/${card.id}`)}
                        className="w-[75px] h-[30px] flex justify-center items-center"    
                    /> 
                </div>
            ))}
        </div>


        {showAddModal && (
        <div className="fixed inset-0 backdrop-blur-xs bg-opacity-10 flex items-center justify-center z-50">
          <div className="bg-white p-6 rounded shadow-lg w-[300px] space-y-4">
            <h2 className="text-lg font-bold">Nhập tên Card</h2>
            <input
              type="text"
              value={newTitle}
              onChange={(e) => setNewTitle(e.target.value)}
              className="w-full border px-3 py-2 rounded focus:outline-none focus:ring-2 focus:ring-blue-300"
              placeholder="Tên card..."
            />
            <div className="flex justify-end gap-2">
              <button
                onClick={() => setShowModal(false)}
                className="px-4 py-2 bg-gray-300 rounded hover:bg-gray-400"
              >
                Hủy
              </button>
              <button
                onClick={handleAddCard}
                className="px-4 py-2 bg-blue-500 text-white rounded hover:bg-blue-600"
              >
                OK
              </button>
            </div>
          </div>
        </div>
      )}

    </div>
  );
}